#Dashboard-background {
  background: url('../../images/topography.png') fixed repeat center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: -2;
}

.card {
  -webkit-box-shadow: 6px 7px 20px -3px rgba(0,0,0,0.75);
  box-shadow: 6px 7px 20px -3px rgba(0,0,0,0.75);
}


h1 {
  color: white;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  text-align: center;
  position:absolute;
  top: 0;
  width: 100%;
  text-shadow:0px 3px 10px #050504;
}

h3 {
  font-family: 'Poiret One', cursive;
  font-weight: bold;
}

#dashboard-footer {
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  opacity: 0.8;
}





