#login-background-initZoom {
  text-align: center;
  background: url("../../images/loginBackground.png") center fixed no-repeat;
  background-size: cover;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: -2;
 -webkit-animation: zoomin 12s ease-out;
 -webkit-animation-fill-mode: forwards;
         animation-fill-mode: forwards;
}

@-webkit-keyframes zoomin {
  0% {
    -webkit-transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.3);
  }
}

.input-field {
  margin-bottom: 0;
}

#signUp {
  text-align: center;
  color: rgba(255, 255, 255, 0.651);
}

#loginInput {
  color: white;
}

#loginInput:focus {
  border-bottom: rgb(40, 162, 233);
}

.signinButton {
  background-color: rgb(40, 162, 233);
  border: 1px solid white;
  margin-bottom: 5%;
  border-radius: 25px;
}

.signinButton:hover {
  color: rgb(40, 162, 233);
  background-color: white;
}

#signinContainer {
  margin-top: 25%;
  background-color: rgba(0, 0, 0, 0.719);
  border-radius: 10px;
  -webkit-box-shadow: 4px 5px 13px 0px rgba(0,0,0,0.75);
  box-shadow: 4px 5px 13px 0px rgba(0,0,0,0.75);
  padding: 0 20px;
}


